.sidebar{
    min-height: 100vh;
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgb(23, 23, 23);
    padding: 25px 15px;
    color: #fff;
}
.sidebar img{
    width: 20px;
}

.sidebar .menu{
    display: block;
    margin-left: 10px;
    cursor: pointer;
    filter: invert(100%);
}
.sidebar .new-chat{
    margin-top: 50px;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    padding: 10px 15px;
    background-color: #e6eaf1;
    border-radius: 50px;
    font-size: 14px;
    color: grey;
    cursor: pointer;
}
.sidebar .recent{
    display: flex;
    flex-direction: column;
    animation: fadeIn 1.5s;
}
.sidebar .recent-title{
    margin-top: 30px;
    margin-bottom: 20px;
}
.sidebar .recent-entry{
    display: flex;
    align-items: start;
    gap: 10px;
    padding: 10px;
    padding-right: 40px;
    border-radius: 50px;
    color: #fff;
    cursor: pointer;
}
.sidebar .recent-entry:hover{
    background-color: #e2e6eb;
    color: black;

}

.sidebar .bottom{
    display: flex;
    flex-direction: column;

}
.sidebar .bottom-item{
    padding-right: 10px;
    cursor: pointer;
    color: #fff;
}
.sidebar .bottom-item img{
    filter: invert(100%);
}
.sidebar .bottom-item img:hover{
    filter: invert(1);
}
@media (max-width:600px){
    .sidebar{
        display: none;
    }
}